<template>
  <div>
    <div class="vld-parent">
      <loading :active.sync="isLoading"></loading>
    </div>
    <div class="text-right mt-4">
      <button type="button" class="btn btn-primary" @click="openModal(true)">建立新的產品</button>
    </div>
    <table class="table mt-4">
      <thead>
        <th width="120">分類</th>
        <th>產品名稱</th>
        <th width="120">原價</th>
        <th width="120">售價</th>
        <th width="100">是否啟用</th>
        <th width="80">編輯</th>
      </thead>
      <tbody>
        <tr v-for="(item, key) in products" :key="key">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-right">
          {{ item.origin_price }}
          </td>
          <td class="text-right">
          {{ item.price }}
          </td>
          <td>
            <span v-if="item.is_enabled" class="text-success">啟用</span>
            <span v-else>未啟用</span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" type="button" @click="openModal(false, item)">編輯</button>
            <button class="btn btn-outline-danger btn-sm" type="button" @click="delModal(item)">刪除</button>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true" >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title" id="exampleModalLabel">
          <span>新增產品</span>
          </h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
          </button>
        </div>

        <div class="modal-body">
            <div class="row">
            <div class="col-sm-4">
              <div class="form-group">
              <label for="image">輸入圖片網址</label>
              <input type="text" class="form-control" id="image"
                  placeholder="請輸入圖片連結" v-model="tempProduct.imageUrl">
              </div>
              <div class="form-group">
              <label for="customFile">或 上傳圖片
                  <font-awesome-icon :icon="['fas', 'spinner']" spin v-if="status.isUploading"/>
              </label>
              <input type="file" id="customFile" class="form-control"
                  ref="files" @change="uploadFile">
              </div>
              <img :src="tempProduct.imageUrl" class="img-fluid" :alt="`${tempProduct.title}商品圖片`">
            </div>
            <div class="col-sm-8">
              <div class="form-group">
              <label for="title">標題</label>
              <input type="text" class="form-control" id="title"
                  placeholder="請輸入標題" v-model="tempProduct.title">
              </div>
              <div class="form-row">
              <div class="form-group col-md-6">
                <label for="category">分類</label>
                <input type="text" class="form-control" id="category"
                placeholder="請輸入分類" v-model="tempProduct.category">
              </div>
              <div class="form-group col-md-6">
                <label for="price">單位</label>
                <input type="unit" class="form-control" id="unit"
                placeholder="請輸入單位" v-model="tempProduct.unit">
              </div>
              </div>
              <div class="form-row">
              <div class="form-group col-md-6">
              <label for="origin_price">原價</label>
                <input type="number" class="form-control" id="origin_price"
                placeholder="請輸入原價" v-model="tempProduct.origin_price">
              </div>
              <div class="form-group col-md-6">
                <label for="price">售價</label>
                <input type="number" class="form-control" id="price"
                placeholder="請輸入售價" v-model="tempProduct.price">
              </div>
              </div>
              <hr>

              <div class="form-group">
              <label for="description">產品描述</label>
              <textarea type="text" class="form-control" id="description"
                  placeholder="請輸入產品描述" v-model="tempProduct.description"></textarea>
              </div>
              <div class="form-group">
              <label for="content">說明內容</label>
              <textarea type="text" class="form-control" id="content"
                  placeholder="請輸入產品說明內容" v-model="tempProduct.content"></textarea>
              </div>
              <div class="form-group">
              <div class="form-check">
                <input class="form-check-input" type="checkbox"
                id="is_enabled" v-model="tempProduct.is_enabled" :true-value="1" :false-value="0">
                <label class="form-check-label" for="is_enabled">
                是否啟用
                </label>
              </div>
              </div>
            </div>
            </div>
        </div>
        <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" @click="updateproduct">確認</button>
        </div>

        </div>
      </div>
    </div>
    <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
        aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
        <div class="modal-header bg-danger text-white">
          <h5 class="modal-title" id="exampleModalLabel">
          <span>刪除產品</span>
          </h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
            是否刪除 <strong class="text-danger">{{ tempProduct.title }}</strong> 商品(刪除後將無法恢復)。
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
          <button type="button" class="btn btn-danger" @click="Delproduct">確認刪除</button>
        </div>
        </div>
      </div>
    </div>
    <Pagination :author="pagination" v-on:increment="getProducts"></Pagination>
  </div>
</template>

<script>
import $ from 'jquery'
import Pagination from '../../components/Pagination.vue'
export default {
  data () {
    return {
      pagination: {},
      products: [],
      tempProduct: {},
      isNew: false,
      isLoading: false,
      status: {
        isUploading: false
      }
    }
  },
  methods: {
    getProducts (page = 1) {
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/products?page=${page}`
      vm.isLoading = true
      vm.$http.get(api).then((response) => {
        vm.isLoading = false
        vm.products = response.data.products
        vm.pagination = response.data.pagination
      })
    },
    openModal (isNew, item) {
      $('#productModal').modal('show')
      if (isNew) {
        this.tempProduct = {}
        this.isNew = true
      } else {
        this.tempProduct = { ...item }
        this.isNew = false
      }
    },
    delModal (item) {
      $('#delProductModal').modal('show')
      this.tempProduct = { ...item }
    },
    Delproduct () {
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempProduct.id}`
      vm.$http.delete(api).then((response) => {
        if (response.data.success) {
          $('#delProductModal').modal('hide')
          vm.getProducts()
        } else {
          $('#delProductModal').modal('hide')
          vm.getProducts()
        }
      })
    },
    updateproduct () {
      const vm = this
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product`
      let httpMethod = 'post'
      if (!vm.isNew) {
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempProduct.id}`
        httpMethod = 'put'
      }
      vm.$http[httpMethod](api, { data: vm.tempProduct }).then((response) => {
        if (response.data.success) {
          $('#productModal').modal('hide')
          vm.getProducts()
        } else {
          $('#productModal').modal('hide')
          vm.getProducts()
        }
      })
    },
    uploadFile () {
      const uploadFile = this.$refs.files.files[0]
      const vm = this
      const formData = new FormData()
      vm.status.isUploading = true
      formData.append('file-to-upload', uploadFile)
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/upload`
      vm.$http.post(url, formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then((response) => {
        vm.status.isUploading = false
        if (response.data.success) {
          vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl)
          vm.$bus.$emit('messsage:push', '上傳成功', 'success')
        } else {
          vm.$bus.$emit('messsage:push', response.data.message, 'danger')
        }
      })
    }
  },
  created () {
    this.getProducts()
  },
  components: {
    Pagination
  }
}
</script>
